import "./style.css";
import { Card } from "primereact/card";

export const PageContentCard = ({
  icon = <i className="pi pi-cog PageContentCard_icon beUpColor" />,
  title = "Default title",
  children,
}) => (
  <Card className="PageContentCard">
    {icon}
    <h3 className="PageContentCard_title">{title}</h3>
    {children}
  </Card>
);
