import { PageHeader } from "../../../../../shared/PageHeader";
import { PageFooter } from "../../../../../shared/PageFooter";
import { useTranslation } from "react-i18next";
import "./style.css";
import { Link } from "react-router-dom";
import { PageContentCard } from "../../../../../shared/PageContentCard";
import { Menu as PsMenu, MenuItem } from "react-pro-sidebar";
import { VacationIcon } from "../../../../../shared/Menu";
import { initialProjectDirectory } from "../../../../../entities/initialProjectDirectory";

const Organization = () => {
  const { i18n } = useTranslation();
  const { t } = i18n;

  return (
    <main className="page settingsOrganization_page container_local">
      <PageHeader title={t("repeatedWords.organization")} />

      <div className="page_content">
        <div className="settingsOrganization_page_content_cards">
          <PageContentCard title={t("repeatedWords.general")}>
            <PsMenu>
              <MenuItem icon={<i className="pi pi-user-plus icon"/>} component={<Link to={`${initialProjectDirectory}/settings/organization/addEmployee`} />}>{t("pages.settings.organization.contentCards.general.items.addEmployee")}</MenuItem>
            </PsMenu>
          </PageContentCard>

          <PageContentCard title={t("menu.subMenuVacations.title")} icon={<VacationIcon secondClassName="PageContentCard_icon"/>}>
          <PsMenu>
              <MenuItem icon={<i className="pi pi-id-card icon"/>} component={<Link to={`${initialProjectDirectory}/settings/organization/vacations/limits`} />}>{t("pages.settings.organization.contentCards.vacations.items.limits")}</MenuItem>
            </PsMenu>
          </PageContentCard>
        </div>
      </div>

      <PageFooter />
    </main>
  );
};

export default Organization;
